import SQS from './connector/sqs-connector';

export default class TrackingService {
  constructor() {
    const sqsConfig = environment.aws.sqs;

    this.storageConnector = new SQS(sqsConfig.QUEUE_URL, sqsConfig.API_VERSION);
  }

  async trackEvent(message, transformer) {
    const data = {
      MessageBody: transformer == null ? JSON.stringify(message) : transformer(message)
    };

    return this.storageConnector.sendMessage(data);
  }
}
