import WebClient from '../../utils/web/web-client';

export default class EyeApiConnector {
  constructor() {
    this.url = `${environment.eye_api.URL}/api/eye`;
  }

  async createUser(body = {}) {
    return WebClient.postJson(`${this.url}/users`, body);
  }

  async createSession(userId, session) {
    return WebClient.postJson(`${this.url}/users/${userId}/sessions`, session);
  }
}
