import TrackingService from './app/service/tracking-service';
import Objects from './app/utils/objects';
import UserService from './app/service/user-service';
import { Event } from './app/model/enum/event';
import SessionService from './app/service/session-service';
import { MessageTransformer } from './app/service/transformer/message-transformer';

/**
 * @namespace SDK
 */

/**
 * Time in seconds to use for interval to ping.
 * @readonly
 * @const {number}
 * @default 30
 * @memberof SDK
 */
const PING_TIME = 30;

/**
 * @class Eye
 * @description
 * Registra e inicializa la cuenta del usuario
 * @memberof SDK
 */
// eslint-disable-next-line import/prefer-default-export
class Eye {
  constructor() {
    this.trackingService = new TrackingService();
    this.userService = new UserService();
    this.sessionService = new SessionService();
  }

  // this service will create a user if it not exist and session in the db.
  // It would determine: region, country, city, isp, ip, connection type, device, browser
  // This service would be called before player is started when the page loads.
  async init(params = {}) {
    // TODO: validate params
    // TODO: authenticate

    if (Objects.empty(params.user)) {
      throw new TypeError('The user object cannot be empty.');
    }

    if (!params.user.accountId || !(await this.userService.isValidAccount(params.user.accountId))) {
      throw new TypeError('The user account is not valid.');
    }

    const user = await this.userService.createUser(params.user);
    let sessionCode;

    try {
      sessionCode = await this.sessionService.create(user.id, params.plugin);
    } catch (error) {
      SessionService.remove(sessionCode);
      throw error;
    }

    return {
      pingTime: PING_TIME,
      code: sessionCode
    };
  }

  async start({
    code,
    title,
    player,
    playerVersion,
    referer,
    resource,
    duration,
    live,
    pingTime,
    timeMark
  } = {}) {
    Objects.required(this.trackingService);

    const msg = {};

    msg.sessionCode = code;
    msg.title = title;
    msg.player = player;
    msg.playerVersion = playerVersion;
    msg.referer = referer;
    msg.resource = resource;
    msg.duration = duration;
    msg.live = live;
    msg.pingTime = pingTime;
    msg.timeMark = timeMark;
    msg.event = Event.START;

    this.trackingService.trackEvent(msg, MessageTransformer.transform);
  }

  async join({ code, timeMark, joinTime } = {}) {
    Objects.required(this.trackingService);

    const msg = {};
    msg.sessionCode = code;
    msg.timeMark = timeMark;
    msg.joinTime = joinTime;
    msg.event = Event.JOIN;

    this.trackingService.trackEvent(msg, MessageTransformer.transform);
  }

  async ping({ code, bitrate, timeMark, pauseDuration } = {}) {
    Objects.required(this.trackingService);

    const msg = {};
    msg.sessionCode = code;
    msg.bitrate = bitrate;
    msg.timeMark = timeMark;
    msg.pauseDuration = pauseDuration;
    msg.event = Event.PING;

    this.trackingService.trackEvent(msg, MessageTransformer.transform);
  }

  async pause({ code, timeMark } = {}) {
    Objects.required(this.trackingService);

    const msg = {};
    msg.sessionCode = code;
    msg.timeMark = timeMark;
    msg.event = Event.PAUSE;

    this.trackingService.trackEvent(msg, MessageTransformer.transform);
  }

  async resume({ code, pauseDuration, timeMark } = {}) {
    Objects.required(this.trackingService);

    const msg = {};
    msg.sessionCode = code;
    msg.pauseDuration = pauseDuration;
    msg.timeMark = timeMark;
    msg.event = Event.RESUME;

    this.trackingService.trackEvent(msg, MessageTransformer.transform);
  }

  async buffering({ code, timeMark } = {}) {
    Objects.required(this.trackingService);

    const msg = {};
    msg.sessionCode = code;
    msg.timeMark = timeMark;
    msg.event = Event.BUFFERING;

    this.trackingService.trackEvent(msg, MessageTransformer.transform);
  }

  async bufferingFinish({ code, timeMark, duration } = {}) {
    Objects.required(this.trackingService);

    const msg = {};
    msg.sessionCode = code;
    msg.timeMark = timeMark;
    msg.duration = duration;
    msg.event = Event.BUFFERING_FINISHED;

    this.trackingService.trackEvent(msg, MessageTransformer.transform);
  }
}

export { PING_TIME, Eye };
