import PassApiConnector from './connector/pass-api-connector';
import EyeApiConnector from './connector/eye-api-connector';
import { StringUtils } from '../utils/string-utils';

export default class UserService {
  constructor() {
    this.passApiConnector = new PassApiConnector();
    this.eyeApiConnector = new EyeApiConnector();
  }

  async isValidAccount(accountId) {
    const account = await this.passApiConnector.retrieveAccount(accountId);

    return account != null && StringUtils.isNotBlank(account.id);
  }

  async createUser(user) {
    if (user == null) {
      throw Error('The user cannot be undefined.');
    }

    if (StringUtils.isBlank(user.userId) || StringUtils.isBlank(user.accountId)) {
      throw Error('The userId/accountId cannot be undefined.');
    }

    return this.eyeApiConnector.createUser(user);
  }
}
