import AWS from 'aws-sdk';

/**
 * @ignore
 * @description
 * Class that manage the connection between the browser and the SQS queue from AWS.
 * @param {string} queueUrl - The `URL` of the queue. Cannot be blank.
 * @param {string} sqsApiVersion - The aws api version of the sqs service. Cannot be blank.
 */
export default class SQSConnector {
  constructor(queueUrl, sqsApiVersion) {
    // Initialize the Amazon Cognito credentials provider
    const cognitoConfig = environment.aws.cognito;

    AWS.config.region = cognitoConfig.REGION;
    AWS.config.credentials = new AWS.CognitoIdentityCredentials({
      IdentityPoolId: cognitoConfig.POOL_ID
    });

    this.sqs = new AWS.SQS({
      params: {
        QueueUrl: queueUrl
      },
      apiVersion: sqsApiVersion
    });
  }

  /**
   * @async
   * @public
   * @description
   * <p><b>Asynchronous</b> method that send some message to the queue. The message
   * cannot be empty or undefined.</p>
   * <p>A message can include only XML, JSON, and unformatted text. The following Unicode characters are allowed:</p>
   * <p><b>#x9 | #xA | #xD | #x20 to #xD7FF | #xE000 to #xFFFD | #x10000 to #x10FFFF</b></p>
   * Any characters not included in this list will be rejected.
   *
   * @param {string} message - The message to send to the queue.
   * @returns {Promise} - The promise that should be evaluated on completion.
   * @example <caption>Successful push</caption>
   */
  async sendMessage(message) {
    return new Promise((resolve, reject) => {
      this.sqs.sendMessage(message, (err, data) => {
        if (err) {
          reject(err);
        } else {
          resolve(data);
        }
      });
    });
  }
}
