import uuid from 'uuid';
import { Objects } from '../utils/objects';
import EyeApiConnector from './connector/eye-api-connector';
import { SessionStatus } from '../model/enum/session-status';

export default class SessionService {
  constructor() {
    this.eyeApiConnector = new EyeApiConnector();
  }

  static async remove(sessionId) {
    localStorage.removeItem(sessionId);
  }

  async create(userId, pluginInfo) {
    const session = {};
    session.time = new Date();
    session.plugin = pluginInfo;
    session.userAgent = navigator.userAgent;
    session.language = navigator.language;
    session.platform = navigator.platform;
    session.device = navigator.userAgent.match(/mobile/i) ? 'mobile-web' : 'desktop';
    session.status = SessionStatus.INITIALIZING;
    session.started = false;

    const sessionId = uuid();
    session.id = sessionId;
    localStorage.setItem(sessionId, JSON.stringify(session));
    let newSession;

    try {
      newSession = await this.eyeApiConnector.createSession(userId, session);

      if (Objects.empty(newSession)) {
        throw new Error('The session could not be created.');
      }
    } catch (err) {
      throw err;
    }

    return newSession.sessionCode;
  }
}
