// eslint-disable-next-line import/prefer-default-export
export class Objects {
  static required(obj, name) {
    if (Objects.notEmpty(obj)) {
      return obj;
    }
    throw new Error(`Object ${name} is null or undefined`);
  }

  static empty(obj) {
    return (
      obj == null ||
      (typeof obj === 'object' && Object.entries(obj).length === 0 && obj.constructor === Object)
    );
  }

  static notEmpty(obj) {
    return !Objects.empty(obj);
  }
}
