import { HttpMethod } from './http-method';
import { MediaType } from './media-type';
import { Header } from './header';

export default class WebClient {
  static async execute(httpMethod = HttpMethod.GET, url = ``, customHeaders, data = {}) {
    const props = {
      method: httpMethod,
      mode: 'cors',
      cache: 'no-cache',
      headers: customHeaders
    };

    if (HttpMethod.GET !== httpMethod) {
      props.body = JSON.stringify(data);
    }

    return fetch(url, props);
  }

  static async postJson(url, data) {
    const headers = {};
    headers[Header.CONTENT_TYPE] = MediaType.APPLICATION_JSON;
    const response = await WebClient.execute(HttpMethod.POST, url, headers, data);

    return response.json();
  }
}
